@import "helper";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  &::before, ::after {
    box-sizing: border-box;
  }
}
ul, ol {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
body {
  max-width: 445px;
  margin: 0 auto;
  font-family: $font-hei;
  font-size: 16px;
  line-height: 1.2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.icon {
  width: 1em;
  height: 1em;
}
